.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
ul{
  display: flex;
  justify-content: center;
  padding: 10px;
  list-style: none;
  font-weight: bold;
}
li{
  margin-right: 20px;

}
h1,h2,h3{
  text-align: center;
}
a.active
{
  color: blue;
}
.my
{
  color:grey;
}
.content
{
  margin: auto;
  width: 500px;
}